.backdrop-spinner {
  width: 100%;
  height: 100%;
  z-index: 40;
  background-color: rgba(0, 0, 0, .8);
  transition-property: opacity;
  transition-duration: 1s;
  position: fixed;
  top: 0;
  left: 0;
}

.loader {
  width: 240px;
  height: 100px;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader div {
  width: 100px;
  height: 100px;
  background: #000;
  border-radius: 50%;
  position: absolute;
  top: 0;
}

.loader .circle-1 {
  background-color: rgba(0, 0, 0, 0);
  background-image: url("logo.268195b3.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  animation: move-1 1.5s ease-in-out infinite alternate;
  left: 220px;
}

.loader .circle-2 {
  background-color: rgba(0, 0, 0, 0);
  background-image: url("logo.268195b3.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  animation: move-2 1.5s ease-in-out 50ms infinite alternate;
  left: 110px;
}

.loader .circle-3 {
  background-color: rgba(0, 0, 0, 0);
  background-image: url("logo.268195b3.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  animation: move-3 1.5s ease-in-out .1s infinite alternate;
  left: 0;
}

@keyframes move-1 {
  from {
    left: 220px;
    transform: rotate(0deg);
  }

  to {
    left: 140px;
    transform: rotate(360deg);
  }
}

@keyframes move-2 {
  from {
    left: 110px;
    transform: rotate(0deg);
  }

  to {
    left: 30px;
    transform: rotate(360deg);
  }
}

@keyframes move-3 {
  from {
    left: 0;
    transform: rotate(0deg);
  }

  to {
    left: -80px;
    transform: rotate(360deg);
  }
}

